.chat__inner {
  display: flex;
  height: 550px;
  .chats {
    width: 30%;
    height: 100%;
    overflow-y: auto;
    .contact {
      cursor: pointer;
    }
  }
  .messages {
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 550px;
    .list {
      overflow-y: auto;
      height: 500px;
      }
    .sendForm {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      .typeMsgInput {
        width: 100%;
      }
      .sendMsgBtn {
        height: 100%;
        width: 100px;
        margin-left: 20px;
        position: relative;
        bottom: 10px;
        padding-top: 10px;
      }
    }
  }
}

.container{
  max-width: 900px;
  .googleLog{
    margin-top: 50px;
  }
}